import http from '../../common/http.js'

export default {
	data() {
		return {
			imgUrl: http.imgUrl,
			list: [],
			goods_id: null,
			goods: '',
			isMember: true,
			shop: {},
			show1: false,
			show2: false,
			show3: false,
			goods_option:'',
			collect: null,
			totalPrices: null,
			unitPrice: null, //单价，用于步进器改变计算总价
			num: 1,
			isbuy: 1,
			spec_obj: {}, //选中的规格对象
		}
	},
	onLoad: function(e) {
		console.log(e)
		this.goods_id = e.goods_id

	},
	onShow: function() {
		this.allRequest()
	},
	onPullDownRefresh: function() {

		// uni.stopPullDownRefresh(); //停止下拉刷新动画
	},
	onReachBottom: function() {

	},
	methods: {
		allRequest: function() {
			this.getGoodsDetail()
		},
		//获取商品详情
		getGoodsDetail: function() {
			let _this = this
			let opts = {
				url: 'Partner/goodDetail',
				method: 'post'
			};
			let param = {
				city_id: '370700',
				user_id: '1629',
				// goods_id: 1840
				goods_id: _this.goods_id
			};
			http.httpTokenRequest(opts, param).then(res => {
				console.log(res.data);
				_this.goods = res.data.data
				_this.goods_option = res.data.data.goods_desc.replace(/\<img/gi, '<img style="max-width:100%;height:auto" ');
				_this.shop = res.data.data.shop
				_this.collect = res.data.collect
				let picArr = res.data.data.many_pic
				let list = []
				for (let i in picArr) {
					let itemObj = {
						image: _this.imgUrl + picArr[i]
					}
					list.push(itemObj)
				}
				_this.list = list


			}, error => {
				console.log(error);
			})
		},
		goIndex: function() {
			uni.switchTab({
				url: '../index/index'
			})
		},
		//拨打电话
		callPhone: function() {
			let _this = this
			uni.showModal({
				title: '提示',
				content: "您确定要拨打" + _this.shop.mobile + "吗",
				success: function(res) {
					if (res.confirm) {
						uni.makePhoneCall({
							phoneNumber: _this.shop.mobile
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}

			})
		},
		_show: function(e) {
			e == 1 ? this.show1 = 1 : e == 2 ? this.show2 = 1 : this.show3 = 1,
				this.isbuy = 1
		},
		_close: function() {
			this.show1 = this.show2 = this.show3 = 0
		},
		cart: function() {
			this.isbuy = false
			this.show2 = 1
		},
		_buy: function() {
			this.isbuy = 1
			this.show2 = 1
		},
		collectGoods: function() {

		},
		chooseSpec: function(e) {
			console.log(e)
			this.spec_obj = e
			this.totalPrices = e.price
			this.unitPrice = e.price //单价，用于步进器改变计算总价
		},
		numBoxPlus: function(e) {
			console.log(e)
		},
		numBoxChange: function(e) {
			console.log(e)
			this.num = e.value
			this.totalPrices = this.accMul(this.unitPrice, this.num)
		},
		accMul: function(arg1, arg2) {
			var m = 0,
				s1 = arg1.toString(),
				s2 = arg2.toString();
			try {
				m += s1.split(".")[1].length
			} catch (e) {}
			try {
				m += s2.split(".")[1].length
			} catch (e) {}

			return (Number(s1.replace(".", "")) * Number(s2.replace(".", "")) / Math.pow(10, m)).toFixed(2);
		}


	}
}
